:root {
    --primary: #e278c8;
    --primary-light: #7699FF;
    --dark: #1C212E;
    --grey-dark: #3F4656;
    --grey: #6C7486;
    --grey-light: #e278c8;
    --white: #FFF;
    --green: #16BF78;
    --sand: #DCB773;
    --sand-light: #EDD9A9;
    --gx-color-accent2: #933468;
    --line-btn-order: rgb(201, 74, 184);
  }
  
  .orderCar {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border: 0;
    background: var(--dark);
    position: relative;
    height: 53px;
    width: 270px;
    padding: 0;
    outline: none;
    cursor: pointer;
    /* border-radius: 32px; */
    -webkit-mask-image: -webkit-radial-gradient(white, black);
    -webkit-tap-highlight-color: transparent;
    overflow: hidden;
    transition: transform 0.3s ease;
    pointer-events: auto;
    &>span {
      position: absolute;
      display: block;
    }
    &>span:nth-child(1) {
      top: 0;
      left: -100%;
      width: 100%;
      height: 3px;
      background: linear-gradient(90deg, transparent, var(--line-btn-order));
      animation: btn-anim1 1s linear infinite;
    }
    &>span:nth-child(2) {
      top: -100%;
      right: 0;
      width: 3px;
      height: 100%;
      background: linear-gradient(180deg, transparent, var(--line-btn-order));
      animation: btn-anim2 1s linear infinite;
      animation-delay: .25s
    }
    &>span:nth-child(3) {
      bottom: 0;
      right: -100%;
      width: 100%;
      height: 3px;
      background: linear-gradient(270deg, transparent, var(--line-btn-order));
      animation: btn-anim3 1s linear infinite;
      animation-delay: .5s
    }
    &>span:nth-child(4) {
      bottom: -100%;
      left: 0;
      width: 3px;
      height: 100%;
      background: linear-gradient(360deg, transparent, var(--line-btn-order));
      animation: btn-anim4 1s linear infinite;
      animation-delay: .75s
    }
  }

  .orderCar .disable{
    background: var(--grey-dark);
    cursor: not-allowed;
    pointer-events: none;
  }

  .oder-main{
    width: 100%;
    height: 53px;
    background-color: --dark;
    display: flex;
    align-items: center;
    justify-content: center;

  }

  .orderCar strong {
    --o: 1;
    position: absolute;
    left: 0;
    right: 0;
    text-align: center;
    top: 16px;
    line-height: 24px;
    color: var(--white);
    font-size: 16px;
    font-weight: 500;
    opacity: var(--o);
    transition: opacity 0.3s ease;
  }
  .orderCar strong.default {
    transition-delay: 0.3s;
  }
  .orderCar strong.success {
    --offset: 16px;
    --o: 0;
  }
  .orderCar strong.success svg {
    width: 12px;
    height: 10px;
    display: inline-block;
    vertical-align: top;
    fill: none;
    margin: 7px 0 0 4px;
    stroke: var(--green);
    stroke-width: 2;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-dasharray: 16px;
    stroke-dashoffset: var(--offset);
    transition: stroke-dashoffset 0.3s ease;
  }
  .orderCar:active {
    transform: scale(0.96);
   
  }
  .orderCar .lines {
    opacity: 0;
    position: absolute;
    height: 3px;
    background: var(--white);
    border-radius: 2px;
    width: 6px;
    top: 30px;
    left: 100%;
    box-shadow: 15px 0 0 var(--white), 30px 0 0 var(--white), 45px 0 0 var(--white), 60px 0 0 var(--white), 75px 0 0 var(--white), 90px 0 0 var(--white), 105px 0 0 var(--white), 120px 0 0 var(--white), 135px 0 0 var(--white), 150px 0 0 var(--white), 165px 0 0 var(--white), 180px 0 0 var(--white), 195px 0 0 var(--white), 210px 0 0 var(--white), 225px 0 0 var(--white), 240px 0 0 var(--white), 255px 0 0 var(--white), 270px 0 0 var(--white), 285px 0 0 var(--white), 300px 0 0 var(--white), 315px 0 0 var(--white), 330px 0 0 var(--white);
  }
  .orderCar .disable{
    background-color: #fff;
  }
  .orderCar .back,
  .orderCar .box {
    /* цвет кузова */
    --start: var(--white);
    --stop: var(--grey-light);
    border-radius: 2px;
    background: linear-gradient(var(--start), var(--stop));
    position: absolute;
  }

  .orderCar .car__name{
    position: absolute;
    width: 20px;
    height: 20px;   
    left: 5%;
    z-index: 2;
    top: 11px;
    font-weight: 900;
    display: flex;
    font-size: 18px;
    align-items: center;
    justify-content: center;
    transform: translateX(24px);
    color: var(--gx-color-accent2);
    &::after{
      content: "";
      width: 40px;
      height: 40px;
      position: absolute;
      top: -9px;
      left: -9px;
      background-color: transparent;
      border: 3px solid var(--gx-color-accent2);
      border-radius: 50%;
    }
    &::before {
      content: "";
      width: 30px;
      height: 30px;
      position: absolute;
      top: -4px;
      left: -4px;
      background-color: transparent;
      border: 1px solid var(--gx-color-accent2);
      border-radius: 50%;
    }

   }

  .orderCar .truck {
    width: 60px;
    height: 41px;
    left: 100%;
    z-index: 1;
    top: 11px;
    position: absolute;
    transform: translateX(24px);
  }
  .orderCar .truck:before, .orderCar .truck:after {
    --r: -90deg;
    content: "";
    height: 2px;
    width: 20px;
    right: 58px;
    position: absolute;
    display: block;
    background: var(--white);
    border-radius: 1px;
    transform-origin: 100% 50%; 
    transform: rotate(var(--r));
  }
  .orderCar .truck:before {
    top: 4px;
  }
  .orderCar .truck:after {
    --r: 90deg;
    bottom: 4px;
  }
  .orderCar .truck .back {
    left: 0;
    top: 0;
    width: 60px;
    height: 41px;
    z-index: 1;
  }
  .orderCar .truck .front {
    overflow: hidden;
    position: absolute;
    border-radius: 2px 9px 9px 2px;
    width: 26px;
    height: 41px;
    left: 60px;
  }
  .orderCar .truck .front:before, .orderCar .truck .front:after {
    content: "";
    position: absolute;
    display: block;
  }
  .orderCar .truck .front:before {
    height: 13px;
    width: 2px;
    left: 0;
    top: 14px;
    background: linear-gradient(var(--grey), var(--grey-dark));
  }
  .orderCar .truck .front:after {
    border-radius: 2px 9px 9px 2px;
    background: var(--primary);
    width: 24px;
    height: 41px;
    right: 0;
  }
  .orderCar .truck .front .window {
    overflow: hidden;
    border-radius: 2px 8px 8px 2px;
    background: var(--primary-light);
    transform: perspective(4px) rotateY(3deg);
    width: 22px;
    height: 41px;
    position: absolute;
    left: 2px;
    top: 0;
    z-index: 1;
    transform-origin: 0 50%;
  }
  .orderCar .truck .front .window:before, .orderCar .truck .front .window:after {
    content: "";
    position: absolute;
    right: 0;
  }
  .orderCar .truck .front .window:before {
    top: 0;
    bottom: 0;
    width: 14px;
    background: var(--dark);
  }
  .orderCar .truck .front .window:after {
    width: 14px;
    top: 7px;
    height: 4px;
    position: absolute;
    background: rgba(255, 255, 255, 0.14);
    transform: skewY(14deg);
    box-shadow: 0 7px 0 rgba(255, 255, 255, 0.14);
  }
  .orderCar .truck .light {
    width: 3px;
    height: 8px;
    left: 83px;
    transform-origin: 100% 50%;
    position: absolute;
    border-radius: 2px;
    transform: scaleX(0.8);
    background: #f0dc5f;
  }
  .orderCar .truck .light:before {
    content: "";
    height: 4px;
    width: 7px;
    opacity: 0;
    transform: perspective(2px) rotateY(-15deg) scaleX(0.94);
    position: absolute;
    transform-origin: 0 50%;
    left: 3px;
    top: 50%;
    margin-top: -2px;
    background: linear-gradient(90deg, #f0dc5f, rgba(240, 220, 95, 0.7), rgba(240, 220, 95, 0));
  }
  .orderCar .truck .light.top {
    top: 4px;
  }
  .orderCar .truck .light.bottom {
    bottom: 4px;
  }
  .orderCar .box {
    --start: var(--sand-light);
    --stop: var(--sand);
    width: 21px;
    height: 21px;
    right: 100%;
    top: 21px;
  }
  .orderCar .box:before, .orderCar .box:after {
    content: "";
    top: 10px;
    position: absolute;
    left: 0;
    right: 0;
  }
  .orderCar .box:before {
    height: 3px;
    margin-top: -1px;
    background: rgba(0, 0, 0, 0.1);
  }
  .orderCar .box:after {
    height: 1px;
    background: rgba(0, 0, 0, 0.15);
  }
  .orderCar.animate .default {
    --o: 0;
    transition-delay: 0s;
  }
  .orderCar.animate .success {
    --offset: 0;
    --o: 1;
    transition-delay: 7s;
  }
  .orderCar.animate .success svg {
    transition-delay: 7.3s;
  }
  .orderCar.animate .truck {
    -webkit-animation: truck 10s ease forwards;
            animation: truck 10s ease forwards;
  }
  .orderCar.animate .truck:before {
    -webkit-animation: door1 2.4s ease forwards 0.3s;
            animation: door1 2.4s ease forwards 0.3s;
  }
  .orderCar.animate .truck:after {
    -webkit-animation: door2 2.4s ease forwards 0.6s;
            animation: door2 2.4s ease forwards 0.6s;
  }
  .orderCar.animate .truck .light:before, .orderCar.animate .truck .light:after {
    -webkit-animation: light 10s ease forwards;
            animation: light 10s ease forwards;
  }
  .orderCar.animate .box {
    -webkit-animation: box 10s ease forwards;
            animation: box 10s ease forwards;
  }
  .orderCar.animate .lines {
    -webkit-animation: lines 10s ease forwards;
            animation: lines 10s ease forwards;
  }
  
  @-webkit-keyframes truck {
    10%, 30% {
      transform: translateX(-164px);
    }
    40% {
      transform: translateX(-104px);
    }
    60% {
      transform: translateX(-224px);
    }
    75%, 100% {
      transform: translateX(24px);
    }
  }
  
  @keyframes truck {
    10%, 30% {
      transform: translateX(-164px);
    }
    40% {
      transform: translateX(-104px);
    }
    60% {
      transform: translateX(-224px);
    }
    75%, 100% {
      transform: translateX(24px);
    }
  }
  @-webkit-keyframes lines {
    0%, 30% {
      opacity: 0;
      transform: scaleY(0.7) translateX(0);
    }
    35%, 65% {
      opacity: 1;
    }
    70% {
      opacity: 0;
    }
    100% {
      transform: scaleY(0.7) translateX(-400px);
    }
  }
  @keyframes lines {
    0%, 30% {
      opacity: 0;
      transform: scaleY(0.7) translateX(0);
    }
    35%, 65% {
      opacity: 1;
    }
    70% {
      opacity: 0;
    }
    100% {
      transform: scaleY(0.7) translateX(-400px);
    }
  }
  @-webkit-keyframes light {
    0%, 30% {
      opacity: 0;
      transform: perspective(2px) rotateY(-15deg) scaleX(0.88);
    }
    40%, 100% {
      opacity: 1;
      transform: perspective(2px) rotateY(-15deg) scaleX(0.94);
    }
  }
  @keyframes light {
    0%, 30% {
      opacity: 0;
      transform: perspective(2px) rotateY(-15deg) scaleX(0.88);
    }
    40%, 100% {
      opacity: 1;
      transform: perspective(2px) rotateY(-15deg) scaleX(0.94);
    }
  }
  @-webkit-keyframes door1 {
    30%, 50% {
      transform: rotate(32deg);
    }
  }
  @keyframes door1 {
    30%, 50% {
      transform: rotate(32deg);
    }
  }
  @-webkit-keyframes door2 {
    30%, 50% {
      transform: rotate(-32deg);
    }
  }
  @keyframes door2 {
    30%, 50% {
      transform: rotate(-32deg);
    }
  }
  @-webkit-keyframes box {
    8%, 10% {
      transform: translateX(40px);
      opacity: 1;
    }
    25% {
      transform: translateX(112px);
      opacity: 1;
    }
    26% {
      transform: translateX(112px);
      opacity: 0;
    }
    27%, 100% {
      transform: translateX(0px);
      opacity: 0;
    }
  }
  @keyframes box {
    8%, 10% {
      transform: translateX(40px);
      opacity: 1;
    }
    25% {
      transform: translateX(112px);
      opacity: 1;
    }
    26% {
      transform: translateX(112px);
      opacity: 0;
    }
    27%, 100% {
      transform: translateX(0px);
      opacity: 0;
    }
  }

  
@keyframes btn-anim1 {
  0% {
    left: -100%;
  }
  50%,100% {
    left: 100%;
  }
}


@keyframes btn-anim2 {
  0% {
    top: -100%;
  }
  50%,100% {
    top: 100%;
  }
}


@keyframes btn-anim3 {
  0% {
    right: -100%;
  }
  50%,100% {
    right: 100%;
  }
}

@keyframes btn-anim4 {
  0% {
    bottom: -100%;
  }
  50%,100% {
    bottom: 100%;
  }
}

@keyframes up-down {
	50% {
		margin-top: -20px;
	}
}